import React from 'react';
import { connect } from 'react-redux';
import { getArticles } from '../actions';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import FlatList from '../components/FlatList';
import Spinner from '../components/Spinner';
import ArticleListItem from '../components/ArticleListItem';

import SideBar from '../components/SideBar';
import PageContent from '../components/PageContent';

function mapStateToProps(state) {
  return state;
}

const styles = theme => ({
  root: {
    display: 'flex',
    flexDirection: 'row'
  },
});

class ArticleList extends React.Component {

  constructor(props) {
    super();

    this.state = {
      loading: false,
      items: props.articles || []
    }
  }
  
  componentDidMount() {
    this.refresh();
  }

  refresh = () => {
    this.setState({ loading: true });
    this.props.getArticles();
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.articles !== nextProps.articles) {
      this.setState({ items: nextProps.articles });
    }
    this.setState({ loading: false });
  }

  render() {
    const { loading, items } = this.state;
    const { classes } = this.props;

    return (
      <div className={classes.root}>
        <SideBar />
        <PageContent>
          <FlatList 
            items={items}
            isLoading={loading}
            renderLoading={ <Spinner /> }
            renderEmpty={ () => <div>There are no articles..</div>}
            renderItem={ item => <ArticleListItem item={item} key={item.id} /> }
          />
        </PageContent>
      </div>
    );
  }
}

ArticleList.propTypes = {
  classes: PropTypes.object.isRequired,
};

const styledComponent = withStyles(styles)(ArticleList);
export default connect(mapStateToProps, { getArticles })(styledComponent);