import React from 'react';
import { connect } from 'react-redux';
import { getPortfolioById } from '../actions';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import _ from 'lodash';
import ReactMarkdown from 'react-markdown';

import Spinner from '../components/Spinner';
import SideBar from '../components/SideBar';
import PageContent from '../components/PageContent';

function mapStateToProps(state) {
  return state;
}

const styles = theme => ({
  root: {
    display: 'flex',
    flexDirection: 'row'
  },
});

class PortfolioView extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      loading: false,
      id: props.match.params.id || null,
      item: props.item || {}
    }
  }
  
  componentDidMount() {
    this.refresh();
  }

  refresh = () => {
    const { portfolio } = this.props;
    const { id } = this.state;

    this.setState({ loading: true });

    if (_.isEmpty(portfolio)) {
      // Fetch live data
      this.props.getPortfolioById(id).then((doc => {
        let item = doc.data();
        this.setState({ item, loading: false});
      }))
    } else {
      // Portfolio Props found! Parsing into item...
      let item = _.find(portfolio, { id } )
      this.setState({ item, loading: false });
    }
  }

  render() {
    const { item } = this.state;
    const { classes } = this.props;

    if (_.isEmpty(item)) {
      return <Spinner />;
    } else {
      return (
        <div className={classes.root}>
          <SideBar />
          <PageContent>
            {item.name}
            <br /><br />
            {item.description}
            <br /><br />
            <ReactMarkdown source={item.body} />
            
          </PageContent>
        </div>
      );
    }
  }

}

PortfolioView.propTypes = {
  classes: PropTypes.object.isRequired,
};

const styledComponent = withStyles(styles)(PortfolioView);
export default connect(mapStateToProps, { getPortfolioById })(styledComponent);